 */

.wp-block-mc-react-app {
	.skeleton-loader {
		display: flex;
		flex-direction: column;
		gap: 1rem;
	}

	.skeleton-card {
		width: 100%;
		height: 150px; /* You can adjust this depending on your design */
		background-color: #e0e0e0;
		animation: skeleton-loading 1.5s infinite ease-in-out;
		border-radius: 8px;
	}

	@keyframes skeleton-loading {
		0% {
			background-color: #e0e0e0;
		}
		50% {
			background-color: #f4f4f4;
		}
		100% {
			background-color: #e0e0e0;
		}
	}

}
